@tailwind components;
/* Stop purging. */

/* Your own custom component styles */

/* Start purging... */
@tailwind utilities;
/* Stop purging. */

/* Your own custom utilities */
@layer components {

    .animateIn {
        transform: translate(0%);
        transition: transform 0.5s ease-in-out;
    }

    .animateOut {
        transform: translate(100%);
        transition: transform 0.5s ease-in-out;
    }

    .neonText {
        color: #00ff00;
        text-shadow:
            0 0 7px #00ff00,
            0 0 10px #00ff00,
            0 0 21px #00ff00,
            0 0 42px #00ff00,
            0 0 82px #00ff00,
            0 0 92px #00ff00,
            0 0 102px #00ff00,
            0 0 151px #00ff00;
    }

    .ReactPiano__Key--active {
        background: #00d14d
    }

    .instagram {
        background: #d6249f;
        background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
        box-shadow: 0px 3px 10px rgba(0, 0, 0, .25);
    }


    .initializeNav {
        transform: translate(100%);
    }

}

@font-face {
    font-family: 'Whitney';
    src: url('../../public/fonts/3bdef1251a424500c1b3a78dea9b7e57.woff') format('woff');
}